import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import http from '@/common/request.js';

export default {
	components: {
		uniNavBar
	},

	onShow() {
		this.reloadCourse();
	},
	data() {
		return {
			list_course: []
		};
	},
	methods: {
		// 跳转课程详情页面
		toCourseInfo(course) {
			uni.redirectTo({
				url: `../courseInfo/courseInfo?courseCode=${course.CourseCode}`
			});
		},
		// 刷新所有课程
		reloadCourse() {
			let that = this;

			uni.showLoading({
				mask: true
			});

			http.request({
				url: '/api/Student/GetMyCourseList',
				method: 'POST',
				data: {
					semesterId: '',
					pageIndex: 1,
					orderByPostTime: 2,
					orderByScanCount: 0,
					orderByPraiseCount: 0,
					orderByCollectCount: 0,
					isPublished: 1,
					courseClassId: '',
					classType: 0
				}
			}).then(r => {
				uni.hideLoading();

				r = that.fr(r);

				console.log('reloadCourse==>', r);

				if (r.Code == 200) {
					that.list_course = r.Data.courstList || [];

					console.log('that.list_course==>', that.list_course);
				}
			});
		},
		// 跳转到离线文件页面
		toOffileFiles() {
			uni.navigateTo({
				url: '../offlineFiles/offlineFiles'
			});
		}
	}
};
